import * as Yup from "yup";
export const contactSchema = Yup.object().shape({
  fullname: Yup.string().required("          "),
  photo: Yup.string().url("   ").required("     "),
  mobile: Yup.number().required("       "),
  email: Yup.string()
    .email("     ")
    .required("       "),
  job: Yup.string().nullable(),
  group: Yup.string().required("     "),
});
